.LoginForm
{
  text-align: center;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  width:400px;
  height: 400px;
  
  font-family: 'Epilogue', sans-serif;
font-family: 'Sora', sans-serif;
}
#root
{
  text-align: center;
  display:flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top:100px;
  font-family: 'Epilogue', sans-serif;
font-family: 'Sora', sans-serif;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
